.cover {
  height: 500px;
  width: 300px;
  border-radius: 30px;
  /* background-color: rgb(113, 126, 243); */
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: inset 0 0 20px 3px black;
  -webkit-box-reflect: below 8px -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(70%, transparent), to(rgba(250, 250, 250, 0.1)));
}
